.pickup {
	padding: 24px;
	box-shadow: 0 5px 12px 0 rgba(217, 226, 233, 0.5);
	border-radius: 4px;
	&__wrap {
		display: flex;
		align-items: center;
		width: 100%; }
	&__image {
		height: 56px;
		border-radius: 50%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		flex: 0 0 56px; }
	&__text {
		flex-grow: 1;
		padding-left: 16px; }
	p {
		font-size: 13px;
		line-height: 1.85;
		text-align: left;
		color: $bluey-grey-two; }
	&__title {
		text-transform: uppercase;
		font-size: 14px;
		font-weight: 600;
		line-height: 1.71;
		letter-spacing: 1.2px;
		color: $dark-grey-blue-two; } }
