.field {
  position: relative;
  margin-bottom: 40px;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16);
}

@media only screen and (max-width: 567px) {
  .field {
    margin-bottom: 20px;
  }
}

.field.is-valid .field__check {
  opacity: 1;
  visibility: visible;
}

.field.is-valid .field__check ~ .field__input {
  padding-right: 32px;
}

.field.is-valid .field__card,
.field.is-valid .field__help {
  opacity: 0;
  visibility: hidden;
}

.field__input {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid #e1e5e9;
  padding: 5px 16px;
  color: #2d2c54;
  font-family: 'Open Sans', ssans-serif;
  font-size: 16px;
  line-height: 1;
}

.field__input::placeholder {
  color: rgba(132, 155, 176, 0.5);
}

.field__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity .24s, visibility .24s;
}

.field__check {
  right: 16px;
  font-size: 8px;
  fill: #59c78a;
  opacity: 0;
  visibility: hidden;
}

.field__card {
  right: 8px;
  width: 32px;
}

.field__card ~ .field__input {
  padding-right: 46px;
}

.field__help {
  right: 8px;
  border: 1px solid #59c78a;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  color: #59c78a;
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
}

.field__help ~ .field__input {
  padding-right: 38px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 13px 35px;
  border-radius: 5px;
  background: #434770;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);
  font-family: 'Open Sans', ssans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 1.2px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  transition: color 0.25s, background 0.25s;
}

@media only screen and (max-width: 567px) {
  .btn {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.btn:hover {
  background: #ffffff;
  color: #545abf;
}

.btn[disabled], .btn:disabled {
  background: #434770;
  color: #32325d;
  cursor: not-allowed;
}

.no-touch .btn[disabled]:hover, .no-touch .btn:disabled:hover {
  background: #434770;
  color: #32325d;
}

.btn.is-active {
  background: #ffffff;
  color: #545abf;
}

.btn-control {
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  font-size: 0;
}

.btn-control:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1023px) {
  .btn-control {
    bottom: 0;
  }
}

.btn-control__prev {
  float: left;
}

.btn-control__next {
  float: right;
}

.aside {
  width: 413px;
  min-width: 413px;
  padding: 40px;
}

@media only screen and (max-width: 1160px) {
  .aside {
    width: 100%;
    padding: 40px 20px;
