  display: block;
}

.steps[data-steps-active="2.5"] .steps__control[data-steps-control="2"] .steps__circle {
  display: block;
}

.steps[data-steps-active="2.5"] .steps__line[data-steps-line="1"]:after {
  transform: scale(1);
}

.steps[data-steps-active="2.5"] .steps__line[data-steps-line="2"]:after {
  transform: scale(1);
}

.steps[data-steps-active="3"] .steps__control[data-steps-control="1"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3"] .steps__control[data-steps-control="2"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3"] .steps__control[data-steps-control="3"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3"] .steps__line[data-steps-line="1"]:after {
  transform: scale(1);
}

.steps[data-steps-active="3"] .steps__line[data-steps-line="2"]:after {
  transform: scale(1);
}

.steps[data-steps-active="3"] .steps__line[data-steps-line="3"]:after {
  transform: scale(0.5, 1);
}

.steps[data-steps-active="3.5"] .steps__control[data-steps-control="1"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3.5"] .steps__control[data-steps-control="2"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3.5"] .steps__control[data-steps-control="3"] .steps__circle {
  display: block;
}

.steps[data-steps-active="3.5"] .steps__line[data-steps-line="1"]:after {
  transform: scale(1);
}

.steps[data-steps-active="3.5"] .steps__line[data-steps-line="2"]:after {
  transform: scale(1);
}

.steps[data-steps-active="3.5"] .steps__line[data-steps-line="3"]:after {
  transform: scale(1);
}

.steps[data-steps-active="4"] .steps__control[data-steps-control="1"] .steps__circle {
  display: block;
}

.steps[data-steps-active="4"] .steps__control[data-steps-control="2"] .steps__circle {
  display: block;
}

.steps[data-steps-active="4"] .steps__control[data-steps-control="3"] .steps__circle {
  display: block;
}

.steps[data-steps-active="4"] .steps__control[data-steps-control="4"] .steps__circle {
  display: block;
}

.steps[data-steps-active="4"] .steps__line[data-steps-line="1"]:after {
  transform: scale(1);
}

.steps[data-steps-active="4"] .steps__line[data-steps-line="2"]:after {
  transform: scale(1);
}

.steps[data-steps-active="4"] .steps__line[data-steps-line="3"]:after {
  transform: scale(1);
}

.steps__controls {
  position: relative;
  margin: 0 12px;
  height: 100%;
}

.steps__control {
  position: absolute;
  top: 0;
  width: 24px;
  height: 24px;
  padding: 6px;
  transform: translate3d(-50%, 0, 0);
}

.steps__control:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #2d2c54;
}

.steps__control[data-steps-control="1"] {
  left: 0;
}

.steps__control[data-steps-control="2"] {
  left: 33.33%;
}

.steps__control[data-steps-control="3"] {
  left: 66.66%;
}

.steps__control[data-steps-control="4"] {
  left: 100%;
}

.steps__circle {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  display: block;
  height: 100%;
  border-radius: 50%;
  background-color: #ffffff;
  display: none;
  text-align: center;
  font-size: 0;
}

.steps__circle:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.steps__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 10px;
  fill: #59c78a;
  transform: translateY(1px);
}

.steps__lines {
  position: absolute;
  left: 12px;
  top: 0;
  right: 12px;
  bottom: 0;
  font-size: 0;
}

.steps__line {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  width: 33.33%;
  height: 3px;
}

.steps__line:before, .steps__line:after {
  content: "";
  position: absolute;
  left: 20px;
  right: 20px;
  top: 0;
  bottom: 0;
  border-radius: 1px;
}

.steps__line:before {
  background: rgba(0, 0, 0, 0.1);
}

.steps__line:after {
  background: #ffffff;
  transform-origin: left center;
  transform: scale(0);
}

.steps__line[data-steps-line="1"] {
  left: 0;
}

.steps__line[data-steps-line="2"] {
  left: 33.33%;
}

.steps__line[data-steps-line="3"] {
  left: 66.66%;
}

.pickup {
