  justify-content: space-between;
  line-height: 1.71;
  font-size: 15px;
  color: #8697a8;
}

.text__left-col {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.text__left-col span {
  text-align: left;
  margin-bottom: 17px;
}

.text__right-col {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  font-weight: 600;
}

.text__right-col span {
  text-align: right;
