  }
}

.choice__input:checked ~ .choice__item .choice__title {
  color: #32325d;
}

.choice__input:checked ~ .choice__item .choice__selected {
  opacity: 1;
}

.choice__input:checked ~ .choice__item .choice__price {
  color: #59c78a;
}

.choice__input:checked ~ .choice__item .choice__label {
  transform: translateY(6px) scale(1.47);
  color: #59c78a;
  letter-spacing: .3px;
}

@media only screen and (max-width: 767px) {
  .choice__input:checked ~ .choice__item .choice__label {
    transform: scale(1.27);
  }
}

.choice__input:checked ~ .choice__item .choice__description {
  color: #8996a6;
}

.choice__input:checked ~ .choice__item .choice__description.is-hidden {
  display: block;
}

.choice__input:checked ~ .choice__item .choice__number {
  background: #49477f;
}

.choice__input:checked ~ .choice__item .choice__number span {
  color: #ffffff;
}

.choice__input:checked ~ .choice__item .choice__month {
  display: none;
}

.apply {
  margin-top: 25px;
  padding-right: 34px;
}

.apply .icon-arrow-down {
  fill: #1b2125;
  width: 12px;
}

.apply__form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  box-shadow: -7px 8px 16px 0 rgba(58, 70, 93, 0.07);
}

.apply__search {
  width: 100%;
  padding: 0 17px;
  border-radius: 5px 0 0 5px;
  color: #8697a8;
  font-family: 'Open Sans', ssans-serif;
  font-size: 16px;
  line-height: normal;
}

.apply__search::placeholder {
  font-size: 16px;
  color: #8697a8;
}

.apply__button {
  background: none;
  border: none;
  padding: 14px 22px 12px;
  font-size: 16px;
  background-color: #545abf;
  color: #fff;
  text-transform: uppercase;
  border-radius: 0 5px 5px 0;
}

.contacts {
  display: flex;
  overflow: hidden;
  border-radius: 5px;
  margin-top: 25px;
  box-shadow: 0 8px 25px 0 rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 567px) {
  .contacts {
    flex-direction: column;
  }
}

.contacts__item {
  display: flex;
  width: 50%;
  text-align: center;
}

@media only screen and (max-width: 567px) {
  .contacts__item {
    width: 100%;
  }
}

.contacts__info {
  background: #ffffff;
  padding: 35px 15px 75px;
  flex-direction: column;
}

.contacts__img {
  width: 53px;
  height: 53px;
  padding: 3px;
  background: #fff4eb;
  border-radius: 50%;
  margin: 0 auto 15px;
  text-align: center;
  font-size: 0;
}

.contacts__img:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.contacts__img .img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
}

.contacts__map {
  height: 100%;
  width: 100%;
}

@media only screen and (max-width: 567px) {
  .contacts__map {
    height: auto;
    padding-top: 50%;
  }
}

.contacts__address {
  text-transform: uppercase;
}

.contacts__address span {
  display: block;
  margin-bottom: 8px;
  color: #545abf;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.6;
  letter-spacing: 1.3px;
}

.contacts__address p {
  color: #32325d;
  font-size: 13px;
  font-weight: 600;
  line-height: 1.85;
  letter-spacing: 1.1px;
}

.payment {
  color: #ffffff;
}

.payment__header {
  margin-bottom: 24px;
}

.payment__header:after {
  content: " ";
  display: table;
  clear: both;
}

.payment__subtitle {
  float: left;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.payment__icons {
  float: right;
  font-size: 0;
}

.payment__icon {
  height: 23px;
}

.payment__icon:not(:last-child) {
  margin-right: 8px;
}

.payment__description {
  margin-bottom: 32px;
  opacity: 0.75;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.375;
}

.payment__description strong {
  font-weight: 600;
}
