body {
	min-width: 320px;
	background: #FFF;
	font-family: 'Open Sans', ssans-serif;
	@include f(15px, 400, 1.4); }

.out {
	display: flex;
	@include lmd {
		flex-direction: column; } }
.main,
.aside {
	overflow: auto;
	height: 100vh;
	@include lmd {
		height: auto; } }
.main {
	flex-grow: 1;
	@include gb($twilight, $dark-grey-blue);
	@include lmd {
		padding: 40px 0; } }
.inner {
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	max-width: 845px;
	&__in {
		max-width: 680px;
		margin-left: auto;
		margin-right: auto; } }
.wrapper {
	position: relative;
	padding: 80px 0 195px;
	@include md {
		padding: 0 0 60px; } }

.label {
	display: block;
	padding-bottom: 20px;
	font-size: 16px;
	font-weight: normal;
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $bluey-grey; }

.title {
	font-size: 18px;
	font-weight: 600;
	line-height: 1.33;
	text-align: center;
	color: $dark-grey-blue-two; }

.title-md {
	@include f(22px, null, 1.45);
	text-align: center;
	margin-bottom: 40px;
	color: $white; }
.title-sm {
	@include f(14px, 600, 1.72);
	letter-spacing: 1.2px;
	color: $white; }
