.aside {
	width: 413px;
	min-width: 413px;
	padding: 40px;
	@include lmd {
		width: 100%;
		padding: 40px 20px;
		min-width: auto; }
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		min-height: 100%; }
	&__head {
		border-bottom: 1px 	solid $white-two;
		padding: 0 0 24px;
		margin-bottom: 32px; }
	&__field {
		&:not(:last-child) {
			margin-bottom: 25px; } }
	&__box {
		border-bottom: 1px solid $light-grey; }
	&__wrap {
		margin: 32px 10px; }
	&__row {
		width: 100%; }
	&__hr {
		width: 100%;
		height: 2px;
		background-color: $white-two;
		margin: 6px 0 0;
		border: none; }
	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 10px; } }
